* {
  font-family: 'Raleway';
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: no-repeat center center fixed;
  background-size: cover;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
img {
  width: 60px;
}
.tab-icon {
  border-radius: 15px;
  border: 2px solid black;
  height: 56px;
  margin-right: 5px;
}
